.intro {
  display: flex;
  flex-direction: column;
  height: 2000px;
  width: stretch;
  align-items: center;
  background-color: #00000044;
}

.imgCon {
  background-color: #FFFFFFAA;
  height: 20vw;
  width: 20vw;
  border-radius: 20vw;
  /* border-radius: 30em; */
  /* border-width: 1em; */
  padding: 6px;
  /* padding: 0.5vw; */
  margin-top: 15em;
}

.section {
  display: flex;
  height: fit-content;
  width: 100vw;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  margin-top: 3vw;
}

.section-title {
  display: flex;
  height: fit-content;
  width: fit-content;
  /* font-size: 10em; */
  font-size: 7vw;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 200;
  color: #FFFFFFAA;
}

.section-content {
  display: flex;
  height: fit-content;
  width: fit-content;
  flex-direction: column;
  justify-content: start;
  align-items: flex-start;
}

.section-text {
  /* margin-bottom: 20px; */
  margin-top: 1.9vw;
  display: flex;
  width: fit-content;
  /* font-size: 3em; */
  font-size: 2.1vw;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 200;
  color: #FFFFFFAA;
}

.v-line {
  /* border-left: 6px solid #FFFFFFAA; */
  border-left: 0.3vw solid #FFFFFFAA;
  margin-top: 0.5vw;
  margin-left: 3vw;
  margin-right: 3vw;
  height: 14.5vw;
}